.news-card-list {
  background-color: #F5F6F7;
  width: 100%;
  margin: 0;
  padding: 80px 0;
}

.news-card-list__width {
  width: calc(100% - 104px * 2);
  max-width: 1440px;
  margin: 0 auto;
  padding: 0;
  display: flex;
  flex-direction: column;
}

.news-card-list__title {
  font-family: 'Roboto Slab';
  font-weight: 400;
  font-size: 40px;
  line-height: 46px;
  margin: 0 0 60px;
}

.news-card-list__container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 16px;
  row-gap: 16px;
  width: 100%;
  list-style: none;
  padding: 0;
  margin: 0;
  justify-items: center;
}

.news-card-list__show-more {
  border: none;
  background-color: #ffffff;
  border-radius: 80px;
  width: 288px;
  height: 64px;
  font-family: 'Roboto';
  padding: 0;
  margin: 64px 0 0;
  align-self: center;
  cursor: pointer;
  outline: none;
}

.news-card-list__show-more:hover {
  opacity: .9;
}

@media screen and (max-width: 1400px) {
  .news-card-list__container {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width: 1050px) {
  .news-card-list__width {
    width: calc(100% - 40px * 2);
  }

  .news-card-list__container {
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 8px;
  }
}

@media screen and (max-width: 760px) {
  .news-card-list__container {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width: 525px) {
  .news-card-list__container {
    grid-template-columns: 1fr;
  }
  .news-card-list__width {
    width: calc(100% - 16px * 2);
    align-items: center;
  }
  .news-card-list__title {
    font: 400 30px/34px 'Roboto Slab';
    margin-right: auto;
  }
}